import React from 'react';
import './UserBuyDroplet.scss';
import {Link} from 'react-router';

import DateAndQuotation from '../../../Widgets/DateAndQuotation/components/DateAndQuotation.js';
import WeatherData from '../../../Widgets/WeatherData/components/WeatherData.js';
import OrderBill from '../../../Widgets/OrderBill/components/OrderBill.js';
import Sidebar from '../../Sidebar/components/Sidebar.js';
import Header from '../../../Header/components/Header.js';

import logoImg from '../assets/droplet-logo.png';
import logOutImg from '../assets/logout-icon.png';

class UserBuyDroplet extends React.Component {
    constructor(props) {
        super(props);
    }

    goToIndex = () => {
        this.props.router.push('/');
    }

    goToLogin = () => {
        this.props.router.push('/login');
    }

    goToBuyPayment = () => {
      this.props.router.push('/user-buyDropletPayment');
    }
    dateChange = () => {

    }

    render() {
        return (
          <div className="user-buy-droplet">
              <Header goHome={this.goToIndex} isLoggedIn={true} />

              <div className="container notification-container">
                  <div className="row">
                      <Sidebar />

                      <div className="col-10">
                          <div className="notification-section">
                              <div className="title-section">
                                  <h4 className="title-text">Buy Droplet</h4>
                              </div>
                              <div className="offset-1 col-12 weather-container">
                              <WeatherData startDate={new Date()} endDate={(new Date()).getDate() + 1} />
                              </div>
                              <div className="offset-1 col-10 quotation-container">
                              <DateAndQuotation startDate={new Date()} endDate={(new Date()).getDate() + 1} onDateSelect={this.dateChange}/>
                              <OrderBill onSubmit={this.goToBuyPayment} />
                              </div>
                          </div>

                      </div>
                  </div>
              </div>
          </div>
      );
    }
}
export default UserBuyDroplet;



